.gpt3__features-container__feature {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
  
    margin: 1rem;
  }
  
  .gpt3__features-container__feature-title {
    flex: 1;
    max-width: 180px;
    margin-right: 2rem;
    /* display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    text-align: left;
    margin-right: 5rem; */
  }
  
  .gpt3__features-container__feature-title h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: -0.04em;
    color: #ffffff; 
  }
  
  .gpt3__features-container__feature-title div {
    width: 38px;
    height: 3px;
    left: 192px;
    top: 1488px;
    background: var(--gradient-bar);
    box-shadow: 0 4px 4px rgba(0,0,0,0.25);
    margin-bottom: .5rem;
  }
  
  .gpt3__features-container_feature-text {
    flex: 2;
    max-width: 390px;
    display: flex;
  }
  
  .gpt3__features-container_feature-text p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: var(--color-text);
  
  }
  
  .gpt3__features-container {
    flex: 1.5;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
  }
  
  @media screen and (max-width: 550px) {
    .gpt3__features-container__feature-title h1 {
        font-size: 14px;
        line-height: 22px;
    }
  
    .gpt3__features-container_feature-text p {
        font-size: 12px;
        line-height: 20px;
    }
  
    .gpt3__features-container__feature{
      margin: 1rem 0;
    }
  }
  
  
  @media screen and (max-width: 550px) {
    .gpt3__features-heading h1 {
        font-size: 28px;
        line-height: 38px;
    }
  }