.header {
    display: flex;
    flex-direction: row;
  }
  
  .header-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
  }
  
  .header-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 50px;
    line-height: 75px;
    letter-spacing: -0.04em;
  }

  .header-content p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    color: var(--color-text);
    margin-top: 1.5rem;
  }
  
  .header-image {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .header-image img {
    width: 50%;
    height: 65%;
  }
  
  @media screen and (max-width: 1050px) {
    .header {
        flex-direction: column;
    }
  
    .header-content {
        margin: 0 0 3rem;
    }
  }
  
  @media screen and (max-width: 650px) {
    .header-content h1 {
        font-size: 48px;
        line-height: 60px;
    }
  
    .header-content p {
        font-size: 16px;
        line-height: 24px;
    }
    

  }
  
  @media screen and (max-width: 490px) {

    .header-content {
      align-items: center !important;
    }
    
    .header-content h1 {
        font-size: 36px;
        line-height: 48px;
    }
  
    .header-content p {
        font-size: 14px;
        line-height: 24px;
    }
  
  }